import React from 'react';
import { useTranslation } from "gatsby-plugin-react-i18next";
import IconPlus from './../../images/icon_plus.svg';
import GrafikCloud from './../../images/grafic_cloud.svg';
import GrafikForschung from './../../images/grafic_research.svg';
import GrafikSoftwareentwicklung from './../../images/grafic_softwaredevelopment.svg';
import { Offer, PlusIcon, Grafic } from './OfferElements.js';
import { Anchor, SectionContainer, Title } from '../MainElements';

function OfferSection() {
    const { t } = useTranslation();
    const graphics = [GrafikSoftwareentwicklung, GrafikCloud, GrafikForschung];

    return (
        <SectionContainer>
            <Anchor id={t("offer:sectionId")}/>
            <Title>{t("offer:sectionTitle")}</Title>
            <ul>
                {Array.isArray(t("offer:offerElements", { returnObjects: true })) &&
                    t("offer:offerElements", { returnObjects: true }).map((item, index) => (
                        <Offer key={index} position={item.position}>
                            <span>
                                <PlusIcon src={IconPlus} alt="Plus Icon" />
                                <h1>{item.secondHeadline}</h1>
                                <p>{item.text}</p>
                            </span>
                            <Grafic 
                                src={graphics[index]} 
                                alt="Grafik einer Cloud" 
                                sizeXS={item.pictureSizeXS}
                                sizeS={item.pictureSizeS}
                                sizeM={item.pictureSizeM}
                                sizeL={item.pictureSizeL}
                                alignment={item.verticalPosition}
                                margin={item.margin}/>
                        </Offer>
                    ))
                }
            </ul>
        </SectionContainer>
    );
}

export default OfferSection;
