import styled from 'styled-components';
import contactImage from './../../images/contact_koeln.png'

export const Contact = styled.div`
    display: flex;
    flex-flow: row nowrap;
    justify-content: start;
    align-items: flex-start;

    @media screen and (max-width: ${props => props.theme.iPad}) {
        margin-top: 0;
        flex-flow: column nowrap;
        align-items: center;
    }
`

export const Map = styled.div`
    margin: 0 ${props => props.theme.col1} 24px ${props => props.theme.col4};
    width: ${props => props.theme.col10};
    background-image: url(${contactImage});
    height: 427px;
    background-size: auto;
    background-repeat: no-repeat;
    background-position: 50% 50%;

    @media screen and (max-width: ${props => props.theme.laptop}) {
        margin: 0 ${props => props.theme.col1} 24px ${props => props.theme.col3};
        width: ${props => props.theme.col11};
        height: 352px;
    }
    
    @media screen and (max-width: ${props => props.theme.iPadLandscape}) {
        margin: 0 ${props => props.theme.col1} 24px ${props => props.theme.col3};
        width: ${props => props.theme.col11};
        height: 273px;
    }

    @media screen and (max-width: ${props => props.theme.iPad}) {
        margin: 0 ${props => props.theme.col4} 32px ${props => props.theme.col4};
        width: ${props => props.theme.col16};
        height: 273px;
    }

    @media screen and (max-width: ${props => props.theme.phone}) {
        margin: 0 ${props => props.theme.col2} 24px ${props => props.theme.col2};
        width: ${props => props.theme.col20};
    }
`

export const Address = styled.div`
    padding: 40px 0 0 0;

    @media screen and (max-width: ${props => props.theme.laptop}) {
        padding: 40px 0 0 0;
    }

    @media screen and (max-width: ${props => props.theme.iPadLandscape}) {
        padding: 30px 0 0 0;
    }

    @media screen and (max-width: ${props => props.theme.iPad}) {
        width: 100%;
        padding: 0px ${props => props.theme.col4};
    }

    @media screen and (max-width: ${props => props.theme.phone}) {
        padding: 0px ${props => props.theme.col3};
    }

    a {
        text-decoration: none;
        color: ${props => props.theme.black};
    }
`

export const CircleContainer = styled.div`
    margin: 100px auto;
    width: calc(26px * ${props => props.circleCount});

    @media screen and (max-width: ${props => props.theme.laptop}) {
        margin: 80px auto;
    }

    @media screen and (max-width: ${props => props.theme.iPadLandscape}) {
        width: calc(20px * ${props => props.circleCount});
        margin: 60px auto;
    }

    @media screen and (max-width: ${props => props.theme.iPad}) {
        margin: 40px auto;
    }

    @media screen and (max-width: ${props => props.theme.phone}) {
        width: calc(18px * ${props => props.circleCount});
    }
`

export const Circle = styled.div`
    border: 1px solid #1C3457;
    box-sizing: border-box;
    width: 16px;
    height: 16px;
    border-radius: 8px;
    margin: 0 5px;
    display: inline-block;
    background-color: ${props => (props.filled ? props.theme.darkBlue : props.theme.white)};

    @media screen and (max-width: ${props => props.theme.iPadLandscape}) {
        width: 12px;
        height: 12px;
        border-radius: 6px;
        margin: 0 4px;
    }

    @media screen and (max-width: ${props => props.theme.phone}) {
        width: 10px;
        height: 10px;
        border-radius: 5px;
        margin: 0 4px;
    }
`