import React from 'react';
import { useTranslation } from "gatsby-plugin-react-i18next";
import { ProfileContainer, ProfileRow } from "./ProfileElements";
import bgImg1 from '../../images/grafic_background.svg';
import bgImg2 from '../../images/grafic_philosophy.svg';

function ProfileSection() {
    const { t } = useTranslation();

    return (
        <ProfileContainer id="profile">
            <ProfileRow backgroundImage={bgImg1} position="left">
                <h2>{t("profile:profileElements.0.backgroundText")}</h2>
                <span>
                    <h3>{t("profile:profileElements.0.headline")}</h3>
                    <p>{t("profile:profileElements.0.text")}</p>
                </span>
            </ProfileRow>
            <ProfileRow beige backgroundImage={bgImg2} position="right">
                <span> 
                    <h3>{t("profile:profileElements.1.headline")}</h3>
                    <p>{t("profile:profileElements.1.text")}</p>
                </span>
                <h2>{t("profile:profileElements.1.backgroundText")}</h2>
            </ProfileRow>
        </ProfileContainer>
    );
}

export default ProfileSection;
