import React from "react"
import { useTranslation } from "gatsby-plugin-react-i18next"
import { Contact, Address, Map } from "./ContactElements"
import { Anchor, SectionContainer, Title } from "../MainElements"

// hier vielleicht OpenStreetMap einbetten
function ContactSection() {
    const { t } = useTranslation()

    return (
        <SectionContainer>
            <Anchor id={t("contact:sectionId")} />
            <Title>{t("contact:sectionTitle")}</Title>
            <Contact>
                <Map />
                <Address>
                    <h2>
                        <a href={`mailto:${t("contact:email")}`}>{t("contact:email")}</a>
                    </h2>
                    <br />
                    <p>{t("contact:contacts.0.name")}</p>
                    <p>{t("contact:contacts.0.address")}</p>
                    <p>{t("contact:contacts.0.street")}</p>
                    <p>{t("contact:contacts.0.city")}</p>
                </Address>
            </Contact>
        </SectionContainer>
    )
}

export default ContactSection
