import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import {
  PageCarouselImage,
  PageCarouselItemContainer,
} from "./PageCarouselElements.js"
import { Carousel } from "react-bootstrap"
import { CarouselContainer } from "../CarouselElements"
import { useTranslation } from "gatsby-plugin-react-i18next"

const USE_BIG_IMG_BREAK_POINT = 992

const PageCarousel = () => {
  const { t } = useTranslation()
  const data = useStaticQuery(
    graphql`
      {
        allFile(filter: { name: { regex: "/^start/" } }) {
          edges {
            node {
              base
              childImageSharp {
                gatsbyImageData(layout: FULL_WIDTH)
              }
            }
          }
        }
      }
    `
  )

  const pageCarouselElements = t("pageCarousel:pageCarouselElements", {
    returnObjects: true,
  })

  const getWindowSizeIfPossible = () => {
    if (typeof window !== `undefined`) {
      return window.innerWidth
    }
    return 100
  }
  const [useBigImage, setUseBigImage] = React.useState(
    getWindowSizeIfPossible() > USE_BIG_IMG_BREAK_POINT
  )
  React.useEffect(() => {
    if (typeof window !== `undefined`) {
      const handleWindowResize = () =>
        setUseBigImage(window.innerWidth > USE_BIG_IMG_BREAK_POINT)
      window.addEventListener("resize", handleWindowResize)

      // Return a function from the effect that removes the event listener
      return () => window.removeEventListener("resize", handleWindowResize)
    }
  }, [])

  return (
    <CarouselContainer id="top">
      {Array.isArray(pageCarouselElements) &&
        pageCarouselElements.filter(item => item.active).map(item => (
          <Carousel.Item key={item.image}>
            <PageCarouselItemContainer>
              <h1 dangerouslySetInnerHTML={{ __html: item.text }}></h1>
              {data.allFile.edges
                .filter(
                  edge =>
                    edge.node.base ===
                    `${useBigImage ? item.imageBig : item.image}`
                )
                .map(myImage => (
                  <PageCarouselImage
                    image={myImage.node.childImageSharp.gatsbyImageData}
                    alt={item.imageAlt}
                    key={item.image}
                  />
                ))}
            </PageCarouselItemContainer>
          </Carousel.Item>
        ))}
    </CarouselContainer>
  )
}

export default PageCarousel
