import React, { useRef } from "react"
import { useStaticQuery, graphql } from "gatsby"
import { Anchor, Title } from "../MainElements"
import {
  ReferenceCarouselContainer,
  ReferenceCarouselImage,
  ReferenceCarouselItemContainer,
  ReferenceCarouselLightBoxButton,
  ReferenceLightBoxButtonClose,
  ReferenceLightBoxContainer,
  ReferenceSectionContainer,
} from "./ReferenceElements"
import { Carousel } from "react-bootstrap"
import { useTranslation } from "gatsby-plugin-react-i18next"
import { GatsbyImage } from "gatsby-plugin-image"

function ReferenceSection() {
  const { t } = useTranslation()
  const [selectedImage, setSelectedImage] = React.useState()
  const lightBoxCloseButton = useRef(null)

  const data = useStaticQuery(
    graphql`
      query {
        allFile(filter: { name: { regex: "/^reference/" } }) {
          edges {
            node {
              base
              childImageSharp {
                gatsbyImageData(layout: FULL_WIDTH)
              }
            }
          }
        }
      }
    `
  )
  const referenceElements = t("reference:referenceElements", {
    returnObjects: true,
  })

  const selectImage = img => {
    if (img) {
      document.body.style.overflow = "hidden"
    } else {
      document.body.style.overflow = "visible"
    }
    setSelectedImage(img)
  }

  return (
    <ReferenceSectionContainer>
      <Anchor id={t("reference:sectionId")} />
      <Title>{t("reference:sectionTitle")}</Title>
      <ReferenceCarouselContainer
        id="reference-container"
        interval={selectedImage ? null : 3000}
      >
        {Array.isArray(referenceElements) &&
          referenceElements
            .filter(item => item.active)
            .map(item => (
              <Carousel.Item key={item.image}>
                <ReferenceCarouselItemContainer>
                  {data.allFile.edges
                    .filter(edge => edge.node.base === `${item.image}`)
                    .map(myImage => (
                      <ReferenceCarouselImage
                        key={item.image}
                        onClick={() => {
                          selectImage({
                            data: myImage.node.childImageSharp.gatsbyImageData,
                            alt: item.imageAlt,
                          })
                        }}
                      >
                        <GatsbyImage
                          image={myImage.node.childImageSharp.gatsbyImageData}
                          alt={item.imageAlt}
                        />
                        <ReferenceCarouselLightBoxButton
                          onClick={() => {
                            selectImage({
                              data: myImage.node.childImageSharp
                                .gatsbyImageData,
                              alt: item.imageAlt,
                            })
                          }}
                        ></ReferenceCarouselLightBoxButton>
                      </ReferenceCarouselImage>
                    ))}

                  <div>
                    <h3>{item.headline}</h3>
                    <p dangerouslySetInnerHTML={{ __html: item.text }}></p>
                  </div>
                </ReferenceCarouselItemContainer>
              </Carousel.Item>
            ))}
      </ReferenceCarouselContainer>
      {selectedImage && (
        <ReferenceLightBoxContainer
          onClick={() => {
            selectImage(null)
          }}
          onKeyDown={e => {
            console.log(e)
            var key = e.code || e.key || e.keyCode || e.which
            console.log(key)
            if (key === "Escape" || key === 27) {
              selectImage(null)
            } else if (key === "Tab" || key === 9) {
              e.preventDefault()
              lightBoxCloseButton.current?.focus()
            }
          }}
        >
          <GatsbyImage
            image={selectedImage.data}
            alt={selectedImage.alt}
            objectFit="contain"
            imgStyle={{
              padding: "1rem",
            }}
          ></GatsbyImage>
          <ReferenceLightBoxButtonClose
            ref={lightBoxCloseButton}
            autoFocus={true}
          ></ReferenceLightBoxButtonClose>
        </ReferenceLightBoxContainer>
      )}
    </ReferenceSectionContainer>
  )
}

export default ReferenceSection
