import React from "react"
import { useTranslation } from "gatsby-plugin-react-i18next"
import { JobOffer, JobLink } from "./JobOfferElements"
import { Anchor, SectionContainer, Title } from "../MainElements"

function JobOfferSection() {
  const { t } = useTranslation()

  return (
    <SectionContainer>
      <Anchor id={t("jobOffer:jobOffer.sectionId")} />
      <Title>{t("jobOffer:jobOffer.sectionTitle")}</Title>
      {Array.isArray(
        t("jobOffer:jobOffer.jobOfferElements", { returnObjects: true })
      ) &&
        t("jobOffer:jobOffer.jobOfferElements", { returnObjects: true })
          .filter(item => item.active)
          .map((item, index) => (
            <div key={index}>
              <JobOffer>
                <h3>{t("jobOffer:jobOffer.defaultTitle")}</h3>
                <h1>{item.headline}</h1>
                <p>{item.text}</p>
                <JobLink to={`/job-offer/${item.slug}`}>
                  <button>{t("jobOffer:jobOffer.buttonText")}</button>
                </JobLink>
              </JobOffer>
            </div>
          ))}
    </SectionContainer>
  )
}

export default JobOfferSection
